import Main from './views/main.vue';

const routers = [
    {
        path: '/',
        component: Main,
        children: [
            { path: 'workerManagement', title: {i18n: 'workerManagement'}, name: 'workerManagement', component: resolve => { require(['./views/worker/list.vue'], resolve); } },
            { path: 'taskManagement', title: {i18n: 'taskManagement'}, name: 'taskManagement', component: resolve => { require(['./views/task/list.vue'], resolve); } },
            { path: 'eventManagement', title: {i18n: 'eventManagement'}, name: 'eventManagement', component: resolve => { require(['./views/event/list.vue'], resolve); } }
        ]
    },
    {
        path: '/login',
        name: 'login',
        meta: {
            title: 'Login - ' 
        },
        component: resolve => { require(['./views/login/login.vue'], resolve); }
    },
    {
        path: '/eventManagement/',
        component: Main,
        children: [
            { path: 'filter', name: 'filter', component: resolve => { require(['./views/event/filterList.vue'], resolve); } }
        ]
    },
    {
        path: '/taskManagement/',
        component: Main,
        children: [
            { path: 'push', name: 'push', component: resolve => { require(['./views/task/taskPush.vue'], resolve); } }
        ]
    },
];
export default routers;



